<template>
  <div id="cssTest" class="topTitle">
    안녕 
    <span>
      Remix
    </span>
  </div>
  <div class="hello">
    <a href="https://testnets.opensea.io/assets/rinkeby/0x674f9882d53e15eccae75c02acf5af3d074c5f1b/1/">오픈씨 바로가기</a>
    <div v-if="isInstallMetaMask == true"> 메타마스크 설치됨
    <!-- 메타마스크 연결 버튼-->
      <div>
        <button v-on:click="connectMetaMask"> 메타 마스크 연결 </button>
      </div>
      <div v-if="currentAddress !=null">
        현재 계정 주소 : {{currentAddress}}
        <!-- 컨트랙트 연결 버튼 -->
        <div v-if="connectedContract ==null">
          <button v-on:click="connectContract"> 컨트랙트 연결 </button>
        </div>
        <div v-else>
          <!-- ownerOf 함수를 호출해보자-->
        
          <div class="function">
            <div class="title">
            ownerof
          </div>
          <div class="parameter">
            <span class="parameterName">tokenId</span>
            <input class="parameterInput" type="text" placeholder="tokenId" v-model="ownerOf_tokenId"/>
          </div>
          <div class="buttons">
            <button class="callButton" v-on:click="ownerOf">call</button>
          </div>

            <div class="result" v-if="ownerOf_result != null">
              ownerOf : {{ownerOf_result}}
            </div>
          </div>
          
          <!-- balanceOf 함수를 호출해보자-->
          <div class="function">
            <div class="title">
            balanceof
          </div>
            <button v-on:click="balanceOf">balanceOf</button>
            <input type="text" placeholder="addressowner" v-model="balanceOf_addressowner"/>
            <div class="result" v-if="balanceOf_result != null">
              balanceOf : {{balanceOf_result}}
            </div>
          </div>

          <!-- mint 함수를 호출해보자-->
          <div class="function">
            <div class="title">
            mint
          </div>
           
            <div class="parameters">
            <input class="parameterInput" type="text" placeholder="to" v-model="mint_to" />
            </div>
            <div class="parameters">
            <input class="parameterInput" type="text" placeholder="tokenId" v-model="mint_tokenId" />
            </div>
            <div class="parameters">
            <input class="parameterInput" type="text" placeholder="url" v-model="mint_url" />
            </div>
            <button class="callButton" v-on:click="mint">mint</button>
            <div class="result" v-if="mint_result != null">
              mint : {{mint_result}}
            </div>
          </div>

        </div>
      </div>
    </div>
    <div v-else> 메타마스크 설치 안됨, 설치해주세요 </div>
  </div>
</template>

<script>
import {ethers} from 'ethers';
import abi from "../assets/abi.json";

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      isInstallMetaMask : window.ethereum != null 
      && window.ethereum.isMetaMask == true, 

      currentAddress : null, 

      connectedContract : null,

      //ownerOf에 필요한 변수
      ownerOf_tokenId : null, 
      ownerOf_result : null,

      //balanceOf에 필요한 변수
      balanceOf_addressowner : null, 
      balanceOf_result: null,

      //mint에 필요한 변수
      mint_to : null,
      mint_tokenId : null, 
      mint_url : null, 
      mint_result : null,
    }
  },
  methods:{
    async connectMetaMask(){
      console.log("connectMetaMask 호출");
      var account = await window.ethereum.request({method:'eth_requestAccounts'});
      console.log(account);
      if(account.length > 0){
        this.currentAddress = account[0];
        console.log(this.currentAddress);
      }
      else {
        console.log("계정이 없네!!");
      }
    },
    async connectContract(){
      console.log("connectContract 호출");
      var contractAddress = "0x674f9882D53E15Eccae75c02acf5aF3d074C5f1b";
      var provider = new ethers.providers.Web3Provider(window.ethereum);

      var tempSigner = provider.getSigner();
      var connectedContract = new ethers.Contract(contractAddress, abi, tempSigner);
      console.log(connectedContract);

      this.connectedContract = connectedContract;
  
    },
    async ownerOf(){
      console.log("ownerOf 호출");
      var ownerAddress = await this.connectedContract.ownerOf(this.ownerOf_tokenId);
      console.log(ownerAddress);
      this.ownerOf_result = ownerAddress;
    },

    // 계정이 가지고 있는 토큰 갯수 출력 
    async balanceOf(){
      var result = await this.connectedContract.balanceOf(this.balanceOf_addressowner);
      this.balanceOf_result = result;
      console.log("balanceOf result 호출");
    },

    async mint(){
      console.log("mint 호출");
      try{
        var transaction = await this.connectedContract.mint(
                                  this.mint_to, 
                                  this.mint_tokenId, 
                                  this.mint_url);
        console.log(transaction);
        this.mint_result = "생성대기중";
        await transaction.wait();
        this.mint_result = transaction;
      }
      catch(err){
        console.log(err);
        this.mint_result = err;
      }
    }
  }
}
</script>

